import React, { useState } from 'react'
import { Form } from 'react-bootstrap'
import CopyRightFooter from '../../Components/CopyRightFooter'
import style from '../../shared/style'

const Profile = () => {
    const [profile, setProfile] = useState({
        name: "",
        email: "test@email.com",
        phone: "test",
        password: ""
    })
    return (
        <div id="page-content-wrapper">

            <div class="h-min-100">

                <div class="container-fluid py-2">
                    <div class="row">
                        <div class="col-6 my-auto">
                            <h4 class="py-2 mb-0 font-weight-bold">Profile</h4>
                        </div>
                    </div>
                </div>

                <div class="container-fluid h-min-100" style={style['h-min-100']}>


                    <form method="post">

                        <div class="row">
                            <div class="col-md-6">

                                <div class="form-group row">
                                    <label class="col-form-label col-md-3">Phone</label>
                                    <div class="col-md-9">
                                        <Form.Control disabled type="text" onChange={e => setProfile({...profile, phone : e.value})} class="form-control" name="username" value={profile.phone}/>
                                    </div>
                                </div>

                                <div class="form-group row">
                                    <label class="col-form-label col-md-3">Email</label>
                                    <div class="col-md-9">
                                    <Form.Control disabled type="text" onChange={e => setProfile({...profile, email : e.value})} class="form-control" name="username" value={profile.email}/>
                                    </div>
                                </div>

                                <div class="form-group row">
                                    <label class="col-form-label col-md-3 text-danger">Name</label>
                                    <div class="col-md-9">
                                    <Form.Control type="text" onChange={e => setProfile({...profile, name : e.value})} class="form-control" name="username" value={profile.name}/>
                                    </div>
                                </div>

                                <div class="form-group row">
                                    <label class="col-form-label col-md-3">New Password</label>
                                    <div class="col-md-9">
                                        <Form.Control type="password" name="password" onChange={e => setProfile({...profile, password : e.value})}  placeholder="Set new password" class="form-control"  value={profile.password}/>

                                    </div>
                                </div>

                                <div class="form-group row">
                                    <div class="col-md-9 offset-md-3">
                                        <button type="submit" name="save" class="btn btn-primary">Save</button>
                                    </div>
                                </div>

                            </div>
                        </div>

                    </form>

                </div>

            </div>


            <CopyRightFooter/>
        </div>
    )
}

export default Profile
